@import '~node_modules/modern-normalize/modern-normalize.css';
:root {
    --text-color-primary: #333333;
    --text-color-secondary: #ffffff;
    --color-brand: #28a745;
    --background-color-primary: #ffffff;
    --background-color-blog: #0284d0;
    --background-color-callback: #f4f4f4;
    --background-color-footer: #333333;
    --background-color-header: rgba(0, 0, 0, 0.8);
    --background-gradient-wrapper: rgba(0, 0, 0, 0.4);
    --color-text-hero: #e9e9e9;
    --color-text-other: #808080;
    --border-form-color: #f5f5f5;
}

$timing-function: cubic-bezier(0.4, 0, 0.2, 1);

$breakpoints: (
    'prima': (min-width: 320px),
    'quarta extensum': (max-width: 767px),
    'quinta': (min-width: 768px),
    'major septima': (max-width: 1359px),
    'octava': (min-width: 1360px),
) !default;

body {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    color: var(--text-color-primary);
    background-color: var(--background-color-primary);
}