.hero {
  text-align: center;
  padding-top: 144px;
  padding-bottom: 95px;

  @include respondTo('quinta') {
    padding-top: 407px;
    padding-bottom: 412px;
  }

  @include respondTo('octava') {
    padding-top: 279px;
    padding-bottom: 284px;
  }
}
  
  .hero__title {
    font-weight: 300;
    font-size: 52px;
    line-height: 1.269;
    margin-bottom: 16px;
    color: var(--color-text-hero);
    text-transform: capitalize;

    @include respondTo('quinta') {
      font-size: 55px;
      line-height: 1.2;
      margin-bottom: 25px;
    }
  }
  
  .hero__tagline {
    margin-bottom: 27px;
  
    font-size: 22px;
    line-height: 1.672;
    color: var(--color-text-hero);

    @include respondTo('octava') {
      margin-bottom: 29px;
    }
  }
  
  .hero__button {
    @include centeredFlex;
    @include buttonPrimary(156px, 10px, 9px, var(--text-color-secondary), var(--color-brand));
    margin: 0 auto;

    &:hover, &:focus {
      background-color: var(--color-text-hero);
      color: var(--color-brand);
    }

    @include respondTo('quinta') {
      width: 165px;
    }
  }
  
  .button__icon {
    margin-right: 6px;
    fill: currentColor;
  }